	module.exports = {"data":{"post":{"html":"<p>We decided quite suddenly a few days ago that we'll fly to Minnesota for a weel over 4th of July this year. Grandma and Grandpa will be quite pleased to see Christopher, and the weather at the lake should be good for relaxing.</p>\n<p>Since we're flying through Newark, we've also decided to spend a weekend in New York City on the way home. Picking a hotel is driving me a little crazy, though, given the high prices and number of choices. Suggestions for a good family hotel are welcome. (And, on another note, can the reviews at <a href=\"http://www.tripadvisor.com/Hotels-g60763-New_York_City_New_York-Hotels.html\">tripadvisor.com</a> be trusted?)</p>","fields":{"title":"Spur of the Moment","date":"27 May 2009","url":"https://papascott.de/2009-05-27-spur-of-the-moment/"}}},"pathContext":{"slug":"/2009-05-27-spur-of-the-moment/","prev":{"fields":{"title":"Hybrid Fryer","date":"27 May 2009","slug":"/2009-05-27-hybrid-fryer/"}},"next":{"fields":{"title":"Buzzard Attack!","date":"05 Jun 2009","slug":"/2009-06-05-buzzard-attack/"}}}}

